<template>
  <div>

    <div class="form-group">
      <label>Имя</label>
      <input type="text" class="form-control" v-model="user.firstName" />
    </div>

    <div class="form-group">
      <label>Фамилия</label>
      <input type="text" class="form-control" v-model="user.lastName" />
    </div>

    <div class="form-group">
      <label>Баланс</label>
      <input type="text" class="form-control" v-model="user.balance" />
    </div>

    <div class="form-group">
      <label>Телефон</label>
      <input type="text" class="form-control" v-model="user.phone" />
    </div>

    <div class="form-group">
      <label>Адрес</label>
      <input type="text" class="form-control" v-model="user.address" />
    </div>

    <div class="form-group">
      <label>Компания</label>
      <input type="text" class="form-control" v-model="user.company" />
    </div>

    <div class="form-group">
      <label>Email</label>
      <input type="email" class="form-control" v-model="user.email" />
    </div>

    <div class="form-group">
      <label>Аватарка</label>
      <div><img class="img-thumbnail" :src="user.picture"></div>
      <input type="email" class="form-control" v-model="user.picture" />
      <input type="file" class="hidden" ref="imageUpload" @change="upload">
      <button type="button" @click="selectNewImage">Выбрать новую</button>
    </div>

  </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'UserForm',

    model: {
        prop: 'user'
    },

    props: {
        user: {
            type: Object,
            required: true
        }
    },

    methods: {
        selectNewImage() {
            this.$refs.imageUpload.click();
        },

        upload() {
            const url = 'https://api.imgur.com/3/image';
            const data = new FormData();

            data.append('image', this.$refs.imageUpload.files[0]);

            const config = {
                headers: {
                    'Authorization': 'Client-ID 3bef0b8892d4b04'
                }
            };

            axios.post(url, data, config)
                .then(response => response.data)
                .then(response => {
                    this.user.picture = response.data.link;
                    this.$refs.imageUpload.value = '';
                })
        }
    },
}
</script>

<style>
    .hidden {
        display: none;
    }

    .img-thumbnail {
        max-width: 300px;
    }
</style>


